import {Typography, Box} from "@material-ui/core";
import LinearProgress from "@material-ui/core/LinearProgress";
import { withStyles } from "@material-ui/core/styles";

const BorderLinearProgress = withStyles((theme) => ({
    root: {
      height: 10,
      borderRadius: 5,
    },
    colorPrimary: {
      backgroundColor:
        theme.palette.grey[theme.palette.type === "light" ? 200 : 700],
    },
    bar: {
      borderRadius: 5,
      backgroundColor: theme.palette.secondary.main,
    },
  }))(LinearProgress);

export default function Innercard(props) {
  return (
    <>
      <div style={{ paddingLeft: "4px" }}>
        <Typography variant="caption">Startup’s development stage :</Typography>
        <Typography variant="caption" color="secondary" paragraph>
          {props.startup.sds}
        </Typography>
      </div>
      <div style={{ paddingLeft: "4px" }}>
        <Typography variant="caption">Review Progress</Typography>
        <Box display="flex" alignItems="center">
          <Box width="100%" mr={1}>
            <BorderLinearProgress variant="determinate" value={30} />
          </Box>
          <Box minWidth={35}>
            <Typography variant="body2" color="textSecondary">
              30%
            </Typography>
          </Box>
        </Box>
      </div>
    </>
  );
}
