import Button from "@material-ui/core/Button";
import { useHistory } from "react-router-dom";
var uniqid = require('uniqid');


function Butns(props) {
  const history = useHistory();
  if (props.end === "end") {
    return (
      <div style={{ display: "flex", justifyContent: "flex-end" }}>
        <Button
          variant="outlined"
          color="primary"
          style={{ width: "10%" }}
          onClick={props.handlback}
          key={uniqid()}
        >
          Back
        </Button>
        <Button
          variant="contained"
          color="primary"
          style={{ width: "10%", marginLeft: "10px" }}
          onClick={props.addStartup}
          key={uniqid()}
        >
          Submit
        </Button>
      </div>
    );
  } 
  else if (props.Btype === "Double") {
    return (
      <div style={{ display: "flex", justifyContent: "flex-end" }}>
        <Button
          variant="outlined"
          color="primary"
          style={{ width: "10%" }}
          onClick={props.handlback}
          key={uniqid()}
        > 
          Back
        </Button>
        <Button
          variant="contained"
          color="primary"
          style={{ width: "10%" , marginLeft: "10px"}}
          onClick={props.handlNext}
          key={uniqid()}
        >
          Next
        </Button>
      </div>
    );
  } else if (props.Btype === "Single") {
    return (
      <Button
        variant="outlined"
        key={uniqid()}
        color="primary"
        style={{ width: "50%" }}
        onClick={props.handlNext}
      >
        Next
      </Button>
    );
  }
  if (props.ftype)
    return (
      <Button variant="outlined" color="primary" style={{ width: "50%" }} onClick={() => {
        history.push(`/${props.where}`);
      }} >
        {props.ftype}
      </Button>
    );
}
export default Butns;
