import { preview } from "../../inputdb/preview";
import Typography from "@material-ui/core/Typography";
import Slide from "./slider";
import Button from "@material-ui/core/Button";
import useStyles from "../../styling/style";
import { useEffect, useState } from "react";
import fetchips from "../../fetchingData/fatchdata";
import CircularProgress from "@material-ui/core/CircularProgress";
import { useLocation } from "react-router";
import { Box } from "@material-ui/core";
import  defaultlogo from "../../imgs/user.svg"
import PutInputs from "../Inputs";
import {judge} from "../../inputdb/judge"

const LabelSlider = ({ data }) => {
  const classes = useStyles();
  const dqs = [
    data.epitch,
    data.sds,
    data.dq1,
    data.dq2,
    data.dq3,
    data.dq4,
    data.dq5,
    data.dq6,
    data.dq7,
    data.dq8,
    data.dq9,
    data.dq10,
    data.dq11,
    data.dq12,
    data.dq13,
    data.dq14,
    data.dq15,
  ];
  const logo = data.logo;
  return (
    <>
      <Typography
        variant="h5"
        paragraph
        align="center"
        className={classes.typ}
        // style={{
        //   fontFamily: "Montserrat, sans-serif",
        //   textTransform: "uppercase",
        //   fontWeight: "bold",
        //   opacity: 0.8,
        //   [theme.breakpoints.down("sm")]: {
        //     fontsize: "1rem"
        //   }
        // }}
      >
        {data.Sname}
      </Typography>
      <div className={classes.ppic}>
       {logo ? 
      //  <img src={logo} className={classes.pic} alt='img' /> 
      <div style={{background: `url(${logo})  no-repeat  center/contain `}} className={classes.pic}>
        </div>
       : <img src={defaultlogo} className={classes.defaultlogo} alt='img' /> } 
      </div>
      {dqs.map((dq, index) => (
        <div className={classes.space} key={index + 400}>
          <Typography
            paragraph
            key={index + 100}
            variant="subtitle1"
            className={classes.textrespo}
            style={{
              fontFamily: "Montserrat, sans-serif",
              textTransform: "uppercase",
              fontWeight: "bold",
              opacity: 0.8,
              color: '#343f56'
            }}
            >
            {index + 1} - {preview[index + 1].name}
            {console.log(preview[index].name)}
          </Typography>
          <Typography
            key={index + 200}
            className={classes.textrespo}
            variant="body1"
            align="left"
            paragraph
            style={{
              fontFamily: "Montserrat, sans-serif",
            }}
          >
            - {dq}
          </Typography>
          <Slide key={index + 300} />
        </div>
      ))}
    </>
  );
};

export default function Correction(props) {
  const location = useLocation();
  const setStartups = props.setStartups
  const [loading, setLoading] = useState(true);
  useEffect(() => {
    const getTasks = async () => {
      const id = location.state.id;
      const data = await fetchips(`startup/${id}`);
      setLoading(false)
      setStartups(data);    };
      getTasks();
    window.scroll(0, 0);
  }, [location, setStartups]);
  return (
    <>
      {loading ? (
        <Box align="center">
        <CircularProgress />, 
        </Box>
      ) : (
        <>
          <LabelSlider data={props.data} />
          <div style={{display: "flex", flexDirection: "column", }}>
            <PutInputs stype="inelab"  inputs={judge} btn="none" {...props} />
          </div>
          <div style={{ display: "flex", justifyContent: "flex-end" }}>
            <Button
              variant="contained"
              style={{ width: "10%" , backgroundColor: "#4caf50", color: "white"}}
            >
              Submit
            </Button>
          </div>
        </>
      )}
      {/* <CircularProgress />
      <LabelSlider data={props.data} />
      <div style={{ display: "flex", justifyContent: "flex-end" }}>
        <Button variant="contained" color="primary" style={{ width: "10%" }}>
          Submit
        </Button>
      </div> */}
    </>
  );
}
