import React from 'react';
import PropTypes from 'prop-types';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import Slider from '@material-ui/core/Slider';
import Typography from '@material-ui/core/Typography';
import Tooltip from '@material-ui/core/Tooltip';

const useStyles = makeStyles((theme) => ({
  root: {
      display:"flex", 
      justifyContent: "center",
      alignItems: "flex-start",
      flexDirection: "column",
      padding: theme.spacing(3, 0)
  },
  margin: {
    height: theme.spacing(3),
  },
}));

function ValueLabelComponent(props) {
  const { children, open, value } = props;

  return (
    <Tooltip open={open} enterTouchDelay={0} placement="top" title={value}>
      {children}
    </Tooltip>
  );
}

ValueLabelComponent.propTypes = {
  children: PropTypes.element.isRequired,
  open: PropTypes.bool.isRequired,
  value: PropTypes.number.isRequired,
};


const PrettoSlider = withStyles(theme => ({
  root: {
    color: theme.palette.success.main,
    height: 20,
 
    width: "99%"
  },
  thumb: {
    height: 30,
    width: 30,
    backgroundColor: '#fff',
    border: '2px solid currentColor',
    marginTop: -6.5,
    marginLeft: -12,
    '&:focus, &:hover, &$active': {
        boxShadow: 'inherit',
    },
    [theme.breakpoints.down("sm")]: {
        height: 25,
        width: 25,
      },
  },
  active: {},
  valueLabel: {
    left: 'calc(-50% + 10px)',
    [theme.breakpoints.down("sm")]: {
        left: 'calc(-50% + 6px)',
      },
  },
  track: {
    height: 20,
    [theme.breakpoints.down("sm")]: {
        height: 15,
      },
    borderRadius: "2rem",
  },
  rail: {
    height: 20,
    [theme.breakpoints.down("sm")]: {
        height: 15,
      },
    borderRadius: "2rem",
  },
}))(Slider);



export default function Slide() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
        <Typography variant="caption" align="center" color="textSecondary"  style={{opacity: 0.8,marginTop: "16px", marginBottom:"0", alignSelf: "center"}}paragraph>Rate it From 0 (Failed) to 10 (Excelent)</Typography>
      <PrettoSlider valueLabelDisplay="auto" aria-label="pretto slider" defaultValue={0} step={1} min={0} max={10}/>
    </div>
  );
}
