import { React, useEffect } from "react";
import Typography from "@material-ui/core/Typography";
import { Grid } from "@material-ui/core";
import logo from "../imgs/llogo.svg";
import Copyright from "./copyright";
import AOS from "aos";
import "aos/dist/aos.css";
import { useStyles } from "../styling/footerstyle";

function Footer() {
  useEffect(() => {
    AOS.init();
  }, []);

  const classes = useStyles();
  return (
    <>
      <footer className={classes.footer}>
        <Grid
          container
          alignItems="center"
          justify="center"
          spacing={3}
          className={classes.main}
        >
          <Grid item sm={3}>
            <div className={classes.conlogo}>
              <img
                src={logo}
                className={classes.logo}
                data-aos="zoom-in"
                data-aos-once="false"
                data-aos-duration="1000"
                alt='img'
              />
            </div>
          </Grid>
          <Grid
            container
            item
            md={9}
            alignItems="center"
            justify="center"
            spacing={5}
          >
            <Grid item sm={3}>
              <div
                className={classes.footerContent}
                data-aos="fade-up"
                data-aos-duration="1000"
                data-aos-once="false"
              >
                <Typography
                  variant="h5"
                  color="secondary"
                  className={classes.formhead}
                >
                  Contact Us
                </Typography>
                <p>
                  Mohammed VI Polytechnic University Lot 660, Hay Moulay Rachid
                  Ben Guerir, 43150, Morocco
                </p>
              </div>
            </Grid>
            <Grid item sm={3}>
              <div
                className={classes.footerContent}
                data-aos="fade-up"
                data-aos-once="false"
                data-aos-duration="1400"
              >
                <Typography
                  variant="h5"
                  color="secondary"
                  className={classes.formhead}
                >
                  NAVIGUER
                </Typography>
                <p>
                  Mohammed VI Polytechnic University Lot 660, Hay Moulay Rachid
                  Ben Guerir, 43150, Morocco
                </p>
              </div>
            </Grid>
            <Grid item sm={3}>
              <div
                className={classes.footerContent}
                data-aos="fade-up"
                data-aos-once="false"
                data-aos-duration="1800"
              >
                <Typography
                  variant="h5"
                  color="secondary"
                  className={classes.formhead}
                >
                  CONFIDENTIALITÉ
                </Typography>
                <p>
                  Mohammed VI Polytechnic University Lot 660, Hay Moulay Rachid
                  Ben Guerir, 43150, Morocco
                </p>
              </div>
            </Grid>
            <Grid item sm={3}>
              <div
                className={classes.footerContent}
                data-aos="fade-up"
                data-aos-once="false"
                data-aos-duration="2600"
              >
                <Typography
                  variant="h5"
                  color="secondary"
                  className={classes.formhead}
                >
                  FOLLOW US
                </Typography>
                <p>
                  Mohammed VI Polytechnic University Lot 660, Hay Moulay Rachid
                  Ben Guerir, 43150, Morocco
                </p>
              </div>
            </Grid>
          </Grid>
        </Grid>
        <Copyright />
      </footer>
    </>
  );
}

export default Footer;
