import useStyles from "../../styling/style";
import { React, useEffect } from "react";
import Typography from "@material-ui/core/Typography";
import { useHistory } from "react-router-dom";
import Button from "@material-ui/core/Button";
import Footer from "../Footer";
import { Contactus } from "./Contactus/Contactus";
import { withStyles } from "@material-ui/core/styles";
import { Container, Grid } from "@material-ui/core";
import { Featuers } from "./Features";
// import { Social } from "./Contactus/social";
import rocket from "../../imgs/rocket1.svg";
import AOS from "aos";
import Typing from "react-typing-animation";
import "aos/dist/aos.css";

function Home() {
  useEffect(() => {
    AOS.init({ once: true });
  }, []);

  const ColorButton = withStyles((theme) => ({
    root: {
      fontFamily: "Montserrat, sans-serif",
      fontWeight: "bold",
      color: theme.palette.error.main,
      border: theme.palette.error.main + " 1px solid",
      "&:hover": {
        transition: theme.transitions.create(
          ["background-color", "fontWeight", "color", "letter-spacing"],
          {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.complex,
          }
        ),
        color: "#FFFFFF",
        fontWeight: "bold",
        letterSpacing: 2,
        backgroundColor: "#ee5522",
      },
    },
  }))(Button);
  const history = useHistory();
  const classes = useStyles();
  return (
    <>
      <div className={classes.container}>
        <div className={classes.over}></div>
        <div className={classes.home}>
          <Container maxWidth="lg" align="center" justify="center">
            <Grid container align="left" justify="center">
              <Grid item xs={12} md={6} lg={4} style={{ alignSelf: "center" }}>
                <div>

                <span
                  // variant="h1"
                  // data-aos="zoom-in"
                  // data-aos-duration="1000"
                  className={classes.typo}
                  color="primary"
                  style={{ color: "white" }}
                  >
                  {"Turn your ideas into a "}
                  <Typing className={classes.typing} loop={true} cursorClassName={classes.cursor}>
                      <span   style={{ color: "#ee5522"}}>
                    Startup.
                    <Typing.Delay ms={500} />
                    <Typing.Backspace count={10} />
                    <Typing.Delay ms={500} />
                    Future.
                    <Typing.Delay ms={500} />
                    <Typing.Backspace count={10} />
                    <Typing.Delay ms={500} />
                    Sucsses.
                    <Typing.Delay ms={500} />
                    <Typing.Backspace count={10} />
                    <Typing.Delay ms={500} />
                   
                      </span>
                  </Typing>
                </span>
                  </div>
                <Typography
                  data-aos="zoom-in"
                  data-aos-duration="1100"
                  variant="h6"
                  className={classes.typodesc}
                  align="left"
                  paragraph
                >
                  Accelerate the economic and human development of the African
                  continent, in a sustainable and responsible perspective
                  through applied research, innovation and entrepreneurship.
                </Typography>
                <div className={classes.butcon}>
                  <ColorButton
                    fullWidth
                    data-aos="fade-up"
                    data-aos-duration="1500"
                    size="large"
                    // color="secondary"
                    variant="outlined"
                    className={classes.butitm}
                    onClick={() => {
                      history.push("/Apply");
                    }}
                  >
                    Apply Now
                  </ColorButton>
                </div>
              </Grid>
              <Grid item md={6} lg={8}>
                <img alt="" src={rocket} style={{ opacity: 0.2 }} />
              </Grid>
            </Grid>
          </Container>
        </div>
        <div>
          {/* <div className={classes.shadow}>
            <h1>Features and Contact Goes Here</h1>
          </div> */}
          <Featuers />
          <div className={classes.shadow}>
            <h1>Features and Contact Goes Here</h1>
          </div>
          <div className={classes.cbg}>
            <Contactus />
          </div>
          <div className={classes.shadow}>
            <h1>Features and Contact Goes Here</h1>
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
}
export default Home;
