import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import Divider from "@material-ui/core/Divider";
import Button from "@material-ui/core/Button";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import CardHeader from "@material-ui/core/CardHeader";
import { useEffect } from "react";
import { useHistory } from "react-router-dom";
import defaultlogo from "../../imgs/user.svg";
import Innercard from "./Innercard";

const useStyles = makeStyles((theme) => ({
  icon: {
    marginRight: theme.spacing(2),
  },
  heroContent: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(8, 0, 6),
  },
  heroButtons: {
    marginTop: theme.spacing(4),
  },
  cardGrid: {
    paddingTop: theme.spacing(8),
    paddingBottom: theme.spacing(8),
  },
  card: {
    height: "100%",
    display: "flex",
    flexDirection: "column",
  },
  cardMedia: {
    paddingTop: "56.25%", // 16:9
  },
  cardContent: {
    flexGrow: 1,
  },
  footer: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(6),
  },
}));

export const StartupCard = (props) => {
  const history = useHistory();

  const classes = useStyles();
  return (
    <Card className={classes.card}>
      <CardHeader
        // action={
        //   <IconButton aria-label="settings">
        //     <MoreVertIcon />
        //   </IconButton>
        // }
        title={props.startup.name}
        titleTypographyProps={{
          variant: "subtitle1",
          style: {
            fontFamily: "Montserrat, sans-serif",
            fontWeight: "bold",
            color: "#343f56",
          },
        }}
      />
      <Divider />
      <CardMedia
        className={classes.cardMedia}
        image={props.startup.logo ? props.startup.logo[0] ? props.startup.logo[0] : defaultlogo : defaultlogo}
        title="Image title"
      />
      <Divider />
      <CardContent className={classes.cardContent}>
        <Typography>{props.startup.epitch}</Typography>
      </CardContent>
      <Innercard {...props} />
      <CardActions>
        <Button
          size="small"
          color="primary"
          onClick={() => {
            history.push({
              pathname: "/Account/correction",
              state: {
                id: props.startup.id,
              },
            });
          }}
        >
          Review
        </Button>
      </CardActions>
    </Card>
  );
};

export const Getstartupcard = (props) => {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  return props.data.map((startup, index) => (
    <Grid item key={index} xs={12} md={6} lg={3}>
      <StartupCard startup={startup} key={index} />
    </Grid>
  ));
};
