import { React, useEffect } from "react";
import formsvg from "../imgs/checked.svg";
import Grid from "@material-ui/core/Grid";
import "../../App.css";
import useStyles from "../styling/inputstyle";
import AOS from "aos";
import "aos/dist/aos.css";
import { Link, Typography } from "@material-ui/core";
import CircularProgress from "@material-ui/core/CircularProgress";
import { Box } from "@material-ui/core";
import { useHistory } from "react-router";



const values = [
  "FirstName",
  "LastName",
  "username",
  "password",
  "Rpassword",
  "email",
  "Sname",
  "sds",
  "adresse",
  "num",
  "dq15",
  "dq1",
  "dq2",
  "dq3",
  "dq4",
  "dq5",
  "dq6",
  "dq7",
  "dq8",
  "dq9",
  "dq10",
  "dq11",
  "dq12",
  "dq13",
  "dq14",
];
const clearValues = (setValue) => {
  values.map((elem) => (
    setValue({ target: { name: elem, value: "" } })
  ))
};

function Submited(props) {
  const history = useHistory();
  useEffect(() => {
    AOS.init();
  }, []);

  const classes = useStyles();
  return props.loading ? (
    <Box align="center">
      <CircularProgress />
    </Box>
  ) : (
    <div>
      <Grid
        container
        alignItems="center"
        justify="center"
        spacing={3}
        style={{ padding: "2rem" }}
      >
        <Grid item xs={12}>
          <Box display="flex" justifyContent="center" alignItems="center">
            <img
              data-aos="fade-up"
              src={formsvg}
              alt="sing"
              style={{ width: "5rem" }}
              className={classes.ssvg}
            />
          </Box>
        </Grid>
        <Grid item xs={12} className={classes.centring}>
          <Box align="center">
            <div className={classes.submited}>
              <Typography variant="subtitle2">
                Thank you for filling out your information! We’ve sent you an
                email with your Verification LINK at the email address you
                provided. Please enjoy, and let us know if there’s anything else
                we can help you with. The StartIncub Team
              </Typography>
            </div>
              <Typography>
              If you are not redirected automatically, follow this:
              </Typography>
            <Link
              onClick={() => {
                clearValues(props.setValue);
                props.navigation.go(0);
                history.push("/");
              }}
              style={{cursor: "pointer"}}
            >
              Back to Home
            </Link>
          </Box>
        </Grid>
      </Grid>
    </div>
  );
}

export default Submited;
