import Chart from './Chart';
import Deposits from './Deposits';
import Orders from './Orders';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import clsx from 'clsx';
import { useStyles } from "../../styling/dashbordstyle";
import { useEffect } from "react";


const griditems = [
  {
    title: "Recomanded",
  },
  {
    title: "Strongly recomanded",
  },
  {
    title: "Not recemanded",
  },
  {
    title: "Startup Total",
  },
]


export const DashOverView = ({startup}) => {
    const classes = useStyles();
    useEffect(() => {
        window.scrollTo(0, 0);
      }, []);
    const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);
    return(
        <Grid container spacing={3}>
          {griditems.map((elem, index) => (
                 <Grid item xs={12} md={6} lg={3}>
                 <Paper className={fixedHeightPaper}>
                   <Deposits startup={startup} griditem={elem}/>
                 </Paper>
               </Grid>
          ))}
        <Grid item xs={12}>
          <Paper className={fixedHeightPaper}>
            <Chart />
          </Paper>
        </Grid>
        <Grid item xs={12}>
          <Paper className={classes.paper}>
            <Orders startup={startup}/>
          </Paper>
        </Grid>
      </Grid>
    )

}