import { React } from "react";
import Grid from "@material-ui/core/Grid";
import "../../App.css";
import PutInputs from "../component/Inputs";
import useStyles from "../styling/inputstyle";
import { signin } from "../inputdb/signin";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Avatar from "@material-ui/core/Avatar";
import Link from "@material-ui/core/Link";
import Paper from "@material-ui/core/Paper";
import Box from "@material-ui/core/Box";
import { makeStyles } from "@material-ui/core/styles";
import Copyright from "../component/copyright";
import loginbg from "../imgs/login.jpg";
import { useHistory } from "react-router-dom";

const useStyle = makeStyles((theme) => ({
  root: {
    height: "100vh",
  },
  image: {
    backgroundImage: `url(${loginbg})`,
    backgroundRepeat: "no-repeat",
    backgroundColor:
      theme.palette.type === "light"
        ? theme.palette.grey[50]
        : theme.palette.grey[900],
    backgroundSize: "cover",
    backgroundPosition: "center",
  },
  paper: {
    height: `calc(100vh - 150px)`,
    margin: theme.spacing(8, 4),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "space-between",
    paddingTop: "100px",
  },
  wrapper: {
    width: "100%",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.primary.main,
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
  forg: {
    paddingTop: theme.spacing(10),
    // opacity: 0.6
  },
  bg: {
    backgroundImage: `url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' version='1.1' xmlns:xlink='http://www.w3.org/1999/xlink' xmlns:svgjs='http://svgjs.com/svgjs' width='1440' height='560' preserveAspectRatio='none' viewBox='0 0 1440 560'%3e%3cg mask='url(%26quot%3b%23SvgjsMask1001%26quot%3b)' fill='none'%3e%3cpath d='M379.61 624.69C530.74 572.56 553.97 139.98 781.01 126.91 1008.04 113.84 981.71 196.91 1182.4 196.91 1383.1 196.91 1481.94 127.17 1583.8 126.91' stroke='rgba(178%2c 86%2c 1%2c 0.25)' stroke-width='2'%3e%3c/path%3e%3cpath d='M264.38 599.8C404.13 588.73 497.84 310.41 743.84 308.99 989.84 307.57 983.58 378.99 1223.31 378.99 1463.04 378.99 1581.63 309.17 1702.77 308.99' stroke='rgba(178%2c 86%2c 1%2c 0.25)' stroke-width='2'%3e%3c/path%3e%3cpath d='M630.01 657.64C726.78 638 668.38 407.18 919.35 388.81 1170.32 370.44 1341.41 152.75 1498.03 148.01' stroke='rgba(178%2c 86%2c 1%2c 0.25)' stroke-width='2'%3e%3c/path%3e%3cpath d='M264.41 572.99C462.11 562.61 590.61 220.85 968.75 211.1 1346.9 201.35 1490.7 22.37 1673.1 20.7' stroke='rgba(178%2c 86%2c 1%2c 0.25)' stroke-width='2'%3e%3c/path%3e%3cpath d='M502.54 640.84C642.52 634.95 729.3 413.77 1009.45 401.86 1289.59 389.95 1376.18 167.08 1516.35 161.06' stroke='rgba(178%2c 86%2c 1%2c 0.25)' stroke-width='2'%3e%3c/path%3e%3c/g%3e%3cdefs%3e%3cmask id='SvgjsMask1001'%3e%3crect width='1440' height='560' fill='white'%3e%3c/rect%3e%3c/mask%3e%3c/defs%3e%3c/svg%3e")`,
    backgroundSize: "cover",
  },
  col: {
    color: `${theme.palette.text.primary} !important`,
  },
}));
function Signin(props) {
  const classes = useStyle();
  const history = useHistory();
  const classe = useStyles();
  return (
    <div className={classe.divlogin}>
      <Grid container component="main" className={classes.root}>
        <Grid item xs={false} sm={4} md={7} className={classes.image} />
        <Grid
          item
          xs={12}
          sm={8}
          md={5}
          component={Paper}
          elevation={6}
          square
          className={classes.bg}
        >
          <div className={classes.paper}>
            <div className={classes.wrapper}>
              <Avatar className={classes.avatar}>
                <LockOutlinedIcon />
              </Avatar>
              <span className={classe.formhead}>Sign In</span>
              <PutInputs inputs={signin} {...props} where="Account"/>
              <Grid container className={classes.forg}>
                <Grid item xs>
                  <Link href="#" variant="body2" color="textSecondary">
                    Forgot password?
                  </Link>
                </Grid>
                <Grid item>
                  <Link
                    variant="body2"
                    color="textSecondary"
                    onClick={() => {
                      history.push("/Apply");
                    }}
                    style={{ cursor: "pointer" }}
                  >
                    {"Don't have an account? Sign Up"}
                  </Link>
                </Grid>
              </Grid>
            </div>
            <Box>
              <Copyright />
            </Box>
          </div>
        </Grid>
      </Grid>
    </div>
  );
}

export default Signin;
