import { React, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { useStep } from "react-hooks-helper";
import Inscription from "./component/inscription.js";
import { useForm } from "react-hooks-helper";
import Home from "./component/Home/Home";
import Signin from "./forms/Signin";
import { Switch, Route } from "react-router-dom";
import NotFound from "./component/NotFound.js";
import Dashboard from "./component/dashboard/Dashboard";
import { Container } from "@material-ui/core";
import { isPossiblePhoneNumber } from 'react-phone-number-input'


const steps = [
  { id: "About your Account", indx: 0 },
  { id: "Startup Description", indx: 1 },
  { id: "Market/Intellectual property ", indx: 2 },
  { id: "Financials/Co-founding team", indx: 3 },
  { id: "Preview", indx: 4 },
];

function AppHead(props) {
  // const addStartup = async () => {
  //   const Ntask = {
  //     name: Sname,
  //     desc: dq15
  //   }
  //   const res = await fetch("http://localhost:7000/startup", {
  //     method: "POST",
  //     headers: {
  //       "Content-type": "application/json",
  //     },
  //     body: JSON.stringify(Ntask),
  //   });
  //   const data = await res.json();
  //   props.setStartup([...props.startup, data]);
  //   navigation.next();
  // };
  const useStyles = makeStyles((theme) => ({
    all: {
      [theme.breakpoints.up("md")]: {
        // paddingRight: theme.spacing(20),
        // paddingLeft: theme.spacing(20),
      },
    },
  }));

  const defaultValues = {
    FirstName: "",
    LastName: "",
    username: "",
    password: "",
    Rpassword: "",
    email: "",
    Sname: "",
    epitch: "",
    sds: "Is",
    dq1: "",
    dq2: "",
    dq3: "",
    dq4: "",
    dq5: "",
    dq6: "",
    dq7: "",
    dq8: "",
    dq9: "",
    dq10: "",
    dq11: "",
    dq12: "",
    dq13: "",
    dq14: "",
    dq15: "",
  };

  const [
    {
      FirstName,
      LastName,
      username,
      password,
      Rpassword,
      email,
      Sname,
      epitch,
      sds,
      dq1,
      dq2,
      dq3,
      dq4,
      dq5,
      dq6,
      dq7,
      dq8,
      dq9,
      dq10,
      dq11,
      dq12,
      dq13,
      dq14,
      dq15,
    },
    setValue,
  ] = useForm(defaultValues);

  const { step, navigation } = useStep({
    steps,
    initialStep: 0,
  });
  const [pictures, setPic] = useState("");
  const [Phone , setPhone] = useState("");
  const onDrop = (picture, file) => {
    setPic(file);
  };
  const [Serrors, setErrors] = useState({});
  function pV(password) {
    var re = {
      capital: /[A-Z]/,
      digit: /[0-9]/,
      full: /[A-Za-z0-9]{7,13}$/,
    };
    return (
      re.capital.test(password) &&
      re.digit.test(password) &&
      re.full.test(password)
    );
  }

  var pattern = new RegExp(
    /^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i
  );
  const checkSignup = (values) => {
    let res = {};
    res.FirstName = !/[a-zA-z]{3,}/.test(values.FirstName)
      ? "This field is required 'more then 3 characters'"
      : "";
    res.LastName = !/[a-zA-z]{3,}/.test(values.LastName)
      ? "This field is required 'more then 3 characters'"
      : "";
    res.email = !pattern.test(values.email) ? "Inccorect Email Format" : "";
    res.Phone = Phone  ? !isPossiblePhoneNumber(Phone) ? "Inccorect Phone Format" : "" :"This field is required";
    res.password =
      pV(values.password) === false
        ? "7-13 characters"
        : "";
    res.Rpassword =
      values.Rpassword !== values.password ? "Password Not match" : "";
    res.num = values.num === "" ? "This field is required" : "";
    setErrors(res);
  };
  const checkDgeneral = (values) => {
    let res = {};
    res.Sname = values.Sname === "" ? "This field is required" : "";
    res.epitch = values.epitch === "" ? "This field is required" : "";
    res.sds = values.sds === "" ? "This field is required" : "";
    res.dq1 = values.dq1 === "" ? "This field is required" : "";
    res.dq2 = values.dq2 === "" ? "This field is required" : "";
    res.dq3 = values.dq3 === "" ? "This field is required" : "";
  
    setErrors(res);
  };
  const checkDdescription = (values) => {
    let res = {};
    res.dq4 = values.dq4 === "" ? "This field is required" : "";
    res.dq5 = values.dq5 === "" ? "This field is required" : "";
    res.dq6 = values.dq6 === "" ? "This field is required" : "";
    res.dq7 = values.dq7 === "" ? "This field is required" : "";
    res.dq8 = values.dq8 === "" ? "This field is required" : "";
    res.dq9 = values.dq9 === "" ? "This field is required" : "";
    setErrors(res);
  };
  const checkFindustry = (values) => {
    let res = {};
    
    res.dq10 = values.dq10 === "" ? "This field is required" : "";
    res.dq11 = values.dq11 === "" ? "This field is required" : "";
    res.dq12 = values.dq12 === "" ? "This field is required" : "";
    res.dq13 = values.dq13 === "" ? "This field is required" : "";
    res.dq14 = values.dq14 === "" ? "This field is required" : "";
    res.dq15 = values.dq15 === "" ? "This field is required" : "";
    setErrors(res);
  };
  const classes = useStyles();

  return (
    <Switch>
      <Route exact path="/">
        <Home />
      </Route>
      <Route exact path="/Apply" >
        <Container maxWidth="xl">
          <Inscription
            checkSignup={checkSignup}
            checkDgeneral={checkDgeneral}
            checkDdescription={checkDdescription}
            checkFindustry={checkFindustry}
            Serrors={Serrors}
            setErrors={setErrors}
            setPic={onDrop}
            step={step}
            steps={steps}
            navigation={navigation}
            username={username}
            FirstName={FirstName}
            LastName={LastName}
            email={email}
            Phone={Phone}
            setPhone={setPhone}
            password={password}
            Rpassword={Rpassword}
            Sname={Sname}
            epitch={epitch}
            sds={sds}
            dq1={dq1}
            dq2={dq2}
            dq3={dq3}
            dq4={dq4}
            dq5={dq5}
            dq8={dq8}
            dq6={dq6}
            dq7={dq7}
            dq9={dq9}
            dq10={dq10}
            dq11={dq11}
            dq12={dq12}
            dq13={dq13}
            dq14={dq14}
            dq15={dq15}
            pictures={pictures}
            setValue={setValue}
            setStartup={props.setStartup}
            {...props}
          />
        </Container>
      </Route>
      <Route exact path="/signin">
        <Signin ftype="LOGIN" className={classes.login} Serrors={{}} />
      </Route>
      <Route path="/Account">
        <Dashboard
          startup={props.startup}
          setStartup={props.setStartup}
          startups={props.startups}
          setStartups={props.setStartups}
        />
      </Route>
      <Route component={NotFound} />
    </Switch>
  );
}
export default AppHead;
