import Paper from "@material-ui/core/Paper";
import { DashOverView } from "./DashOverView";
import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import NotFound from "../../component/NotFound.js";
import { Getstartupcard } from "./StartupCard";
import Correction from "./Correction";
import { Switch, Route } from "react-router-dom";
import { useStyles } from "../../styling/dashbordstyle";

export const InnerBoard = ({ startup, startups, setStartups }) => {
  const classes = useStyles();
  return (
    <Container maxWidth="lg" className={classes.container}>
      <Switch >
        <Route exact path="/Account/">
          <DashOverView startup={startup}/>
        </Route>
        <Route path="/Account/correction">
          <Paper className={classes.spaper}>
            <Correction data={startups}  setStartups={setStartups} />
          </Paper>
        </Route>
        <Route path="/Account/startups">
          <Grid container spacing={3} className={classes.getcard}>
            <Getstartupcard data={startup} />
          </Grid>
        </Route>
        <Route component={NotFound} />
      </Switch>
      {/* judege correction  */}
      
    </Container>
  );
};
