import { React, useState, useEffect } from "react";
import clsx from "clsx";
import Drawer from "@material-ui/core/Drawer";
import List from "@material-ui/core/List";
import Divider from "@material-ui/core/Divider";
import IconButton from "@material-ui/core/IconButton";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import { mainListItems } from "./listItems";
import Logo from "../../imgs/llogodash.svg";
import fetchips from "../../fetchingData/fatchdata";
import { useStyles } from "../../styling/dashbordstyle";
import { InnerBoard } from "./InnerBoard.js";
import Box from "@material-ui/core/Box";
import Copyright from "../copyright";

export default function Dashboard({ setStartup, startup, setStartups,startups  }) {
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  useEffect(() => {
    const getTasks = async () => {
      const data = await fetchips("startup");
      setStartup(data);
    };
    getTasks();
    function handleResize() {
      window.innerWidth < 960 ? setOpen(false) : setOpen(true);
    }
    window.addEventListener("resize", handleResize);
    handleResize();
  }, [setStartup]);

  const handleDrawerOpen = () => {
    setOpen(true);
  };
  const handleDrawerClose = () => {
    setOpen(false);
  };
  return (
    <div className={classes.root}>
      <Drawer
        variant="permanent"
        classes={{
          paper: clsx(classes.drawerPaper, !open && classes.drawerPaperClose),
        }}
        open={open}
      >
        <div className={classes.toolbarIcon}>
          <IconButton size="medium" onClick={handleDrawerOpen}>
            <img src={Logo} className={classes.logo} alt='img' />
          </IconButton>
          <IconButton onClick={handleDrawerClose}>
            <ChevronLeftIcon  />
          </IconButton>
        </div>
        <Divider />
        <List >{mainListItems}</List>
      </Drawer>
      <main className={!open ? classes.contentOpen : classes.contentClose}>
        <div className={classes.appBarSpacer} />
        <InnerBoard startup={startup} startups={startups} setStartups={setStartups} />
        <div className={classes.appBarSpacer} />
      <Box pt={4} className={classes.copy}>
        <Copyright />
      </Box>
      </main>
    </div>
  );
}
